<script>
import ArticleInstance from "@/components/ArticleInstance.vue";
import marked from "marked";

export default {
  name: "Repertoire",
  components: {
    ArticleInstance
  },
  computed: {
    repertoireText() {
      return (
        this.$store.state.homePageData?._allRepertoireLocales[
          this.$store.state.selectedLanguageIndex
        ].value
      );
    },
    selectedLanguageIndex() {
      return this.$store.state.selectedLanguageIndex;
    },
    markdownRepertoireText: function() {
      return marked(this.repertoireText, { sanitize: true });
    }
  },
  mounted() {
    console.log(this.$root);
  }
};
</script>

<template>
  <ArticleInstance title="Repertoire" @close="$emit('close')">
    <span class="image main"
      ><img src="@/assets/images/pic02.jpg" alt=""
    /></span>
    <p v-html="markdownRepertoireText" />
  </ArticleInstance>
</template>

<style scoped></style>
